* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --primary: #4285F4;
  --secondary: #34A853;
  --error: #EA4335;
  --warning: #FBBC04;
  --info: #9C27B0;
  --success: #34A853;
  --background: #F5F7FA;
  --surface: #FFFFFF;
  --text-primary: rgba(0, 0, 0, 0.87);
  --text-secondary: rgba(0, 0, 0, 0.6);
  --text-disabled: rgba(0, 0, 0, 0.38);
  --border: rgba(0, 0, 0, 0.12);
}

[data-theme='dark'] {
  --primary: #90CAF9;
  --secondary: #81C784;
  --error: #F48FB1;
  --warning: #FFE082;
  --info: #CE93D8;
  --success: #81C784;
  --background: #121212;
  --surface: #1E1E1E;
  --text-primary: rgba(255, 255, 255, 0.87);
  --text-secondary: rgba(255, 255, 255, 0.6);
  --text-disabled: rgba(255, 255, 255, 0.38);
  --border: rgba(255, 255, 255, 0.12);
}

body[data-theme='light'] {
  --bg: #f5f7fa;
  --text: #333333;
  --text-secondary: #666666;
  --primary: #1976d2;
  --secondary: #6c757d;
  --surface: #ffffff;
  --surface-variant: #f9f9f9;
  --outline: rgba(0, 0, 0, 0.12);
  --outline-variant: rgba(0, 0, 0, 0.08);
  --hover-overlay: rgba(0, 0, 0, 0.04);
  --active-overlay: rgba(0, 0, 0, 0.08);
  --error: #d32f2f;
  --warning: #ed6c02;
  --info: #0288d1;
  --success: #2e7d32;
  --divider: rgba(0, 0, 0, 0.12);
  
  /* Header colors */
  --header-bg: #ffffff;
  --header-text: #333333;
  
  /* Sidebar colors */
  --sidebar-bg: #1a2035;
  --sidebar-text: #ffffff;
  --sidebar-hover-bg: rgba(255, 255, 255, 0.08);
  --sidebar-active-bg: rgba(255, 255, 255, 0.16);
  --sidebar-active-text: #ffffff;
  
  /* Card colors */
  --card-bg: #ffffff;
  --card-text: #333333;
  --card-border: rgba(0, 0, 0, 0.12);
  --card-hover-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  
  /* Notification styling */
  --notification-success-color: #2e7d32;
  --notification-error-color: #d32f2f;
  --notification-warning-color: #ed6c02;
  --notification-info-color: #0288d1;
}

body[data-theme='dark'] {
  --bg: #121212;
  --text: #e0e0e0;
  --text-secondary: #a0a0a0;
  --primary: #90caf9;
  --secondary: #a0aec0;
  --surface: #1e1e1e;
  --surface-variant: #252525;
  --outline: rgba(255, 255, 255, 0.12);
  --outline-variant: rgba(255, 255, 255, 0.08);
  --hover-overlay: rgba(255, 255, 255, 0.04);
  --active-overlay: rgba(255, 255, 255, 0.08);
  --error: #f44336;
  --warning: #ff9800;
  --info: #29b6f6;
  --success: #66bb6a;
  --divider: rgba(255, 255, 255, 0.12);
  
  /* Header colors */
  --header-bg: #1e1e1e;
  --header-text: #e0e0e0;
  
  /* Sidebar colors */
  --sidebar-bg: #111827;
  --sidebar-text: #e0e0e0;
  --sidebar-hover-bg: rgba(255, 255, 255, 0.08);
  --sidebar-active-bg: rgba(255, 255, 255, 0.16);
  --sidebar-active-text: #ffffff;
  
  /* Card colors */
  --card-bg: #1e1e1e;
  --card-text: #e0e0e0;
  --card-border: rgba(255, 255, 255, 0.12);
  --card-hover-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
  
  /* Notification styling */
  --notification-success-color: #66bb6a;
  --notification-error-color: #f44336;
  --notification-warning-color: #ff9800;
  --notification-info-color: #29b6f6;
}

a {
  color: var(--primary);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Custom scrollbar for better UX */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 20px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

body[data-theme='dark']::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.2);
}

body[data-theme='dark']::-webkit-scrollbar-thumb:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Notification icon styling */
.notification-icon {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 8px;
}

.notification-icon.success {
  background-color: var(--notification-success-color);
}

.notification-icon.error {
  background-color: var(--notification-error-color);
}

.notification-icon.warning {
  background-color: var(--notification-warning-color);
}

.notification-icon.info {
  background-color: var(--notification-info-color);
}
